import { createEl, createBtn } from "./createFunc.js";

// –ü–ª–∞–≤–Ω–æ–µ —É–¥–∞–ª–µ–Ω–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –∏–∑ DOM
export const modalRemove = (el) => {
  el.setAttribute("style", "animation: removeBlock .5s forwards");
  setTimeout(() => el.remove(), 500);
};

export const modalClientsDel = () => {
  const modal = createEl("div", "modal", "modal");
  const modalContainer = createEl("div", "modal__container", "modal-container");
  const modalTitleBlock = createEl("div", "modal__title-block");
  const modalTitle = createEl("h3", "modal__title");
  const closeBtnX = createBtn("modal__close-x", "&#10006");
  const modalContent = createEl("div", "modal__content");
  const modalText = createEl("p", "deleteText");
  const btnDeleteModal = createBtn("btn", "–£–¥–∞–ª–∏—Ç—å");
  const modalCloseBtn = createBtn("btn-canc", "–û—Ç–º–µ–Ω–∏—Ç—å");

  modalTitle.textContent = "–£–¥–∞–ª–∏—Ç—å —É—á–∞—Å—Ç–Ω–∏–∫–∞";
  modalText.textContent = "–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –¥–∞–Ω–Ω–æ–≥–æ —É—á–∞—Å—Ç–Ω–∏–∫–∞";

  document.body.append(modal);
  modal.append(modalContainer);
  modalContainer.append(
    closeBtnX,
    modalTitleBlock,
    modalTitle,
    modalContent,
    btnDeleteModal,
    modalCloseBtn
  );
  modalTitleBlock.append(modalTitle);
  modalContent.append(modalText);

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  modalCloseBtn.addEventListener("click", () => {
    modalRemove(modal);
  });

  // –ó–∞–∫—Ä—ã—Ç—å –Ω–∞ –∫—Ä–µ—Å—Ç–∏–∫
  closeBtnX.addEventListener("click", () => {
    modalRemove(modal);
  });
  // –ó–∞–∫—Ä—ã—Ç—å –Ω–∞ –∫–Ω–æ–ø–∫–æ–π esc
  window.addEventListener("keydown", (e) => {
    if (e.key === "Escape") {
      modalRemove(modal);
    }
  });
  // –ó–∞–∫—Ä—ã—Ç—å –Ω–∞ –∫–ª–∏–∫–æ–º –≤–Ω–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  document
    .querySelector("#modal .modal__container")
    .addEventListener("click", (e) => {
      e._isClickWithInModal = true;
    });
  modal.addEventListener("click", (e) => {
    if (e._isClickWithInModal) return;
    modalRemove(e.currentTarget);
  });

  return { modal, btnDeleteModal };
};


